.container {
   display: grid;
   grid-template-columns: minmax(650px, auto) 1fr;
   gap: 10px;
   padding: 10px;
  
 }
 
 /* Left column for text content */
 .text-column {
   padding: 20px;
   
 }
 
 .text-column h2 {
   font-size: 40px;
   margin-bottom: 10px;
   font-weight: 300;
 }
 

 /* Right column for video content */
 .video-column {
   padding: 40px;
   border-radius: 5px;
 }
 
 .video-column iframe {
   width: 100%;
   height: 100%;
   border: 5px;
   border-radius: 30px;
 }

 span {
   color: #cfa04d;
   font-weight: 400;
 }

 /* Media query for screens smaller than 768px */
@media (max-width: 768px) {
   .container {
   
     grid-template-columns: 1fr; /* Single column for smaller screens */
   }
   
   /* Adjust individual column styles for smaller screens */
   .text-column {
     margin-bottom: 20px; /* Add some space between the columns */
   }

   .text-column h2{
      font-size: 35px;
      align-items: center;
      justify-content: center;
      text-align: center;
   }
 
   .video-column {
     margin-top: 40%;
     margin-bottom: 5px; /* Add some space between the columns */
   }
 }
 
 /* Media query for screens smaller than 576px */
 @media (max-width: 356px) {
   .text-column,
   .video-column {
      margin-top: 40%;
      margin-bottom: 10px;
     padding: 10px; /* Reduce padding for smaller screens */
   }

   .video-column iframe {
      width: 100%;
      height: 100%;
   }
 }