@import url('https://fonts.googleapis.com/css2?family=Jost:wght@200;300;500;600;700;800;900&family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Jost', sans-serif;
    background-color: #dfdede;
}

a {
    text-decoration: none;
}

ul {
    list-style-type: none;
}

.flexSB {
    display: flex;
    justify-content:space-between;
    align-items: center;
}

.container {
    max-width: 90%;
    margin: auto;
}

button {
    background-color: #cfa04d;
    outline: none;
    border: none;
    color: #fff;
    padding: 12px 40px;
    font-weight: 500;
    font-size: 17px;
}

.row {
   width: 50% 
}

p {
    line-height: 30px;
}

.flex {
    display: flex;
}

.control-btn button {
    background: none;
    padding: 0;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid rgba(255, 255, 255, 0.3);
}

.control-btn .next {
    position: absolute;
    top: 50%;
    right: 2%;
}

.control-btn .prev {
    position: absolute;
    top: 50%;
    left: 2%;
    z-index: 99;
}