@import url(https://fonts.googleapis.com/css2?family=Jost:wght@200;300;500;600;700;800;900&family=Poppins:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Jost', sans-serif;
    background-color: #dfdede;
}

a {
    text-decoration: none;
}

ul {
    list-style-type: none;
}

.flexSB {
    display: flex;
    justify-content:space-between;
    align-items: center;
}

.container {
    max-width: 90%;
    margin: auto;
}

button {
    background-color: #cfa04d;
    outline: none;
    border: none;
    color: #fff;
    padding: 12px 40px;
    font-weight: 500;
    font-size: 17px;
}

.row {
   width: 50% 
}

p {
    line-height: 30px;
}

.flex {
    display: flex;
}

.control-btn button {
    background: none;
    padding: 0;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid rgba(255, 255, 255, 0.3);
}

.control-btn .next {
    position: absolute;
    top: 50%;
    right: 2%;
}

.control-btn .prev {
    position: absolute;
    top: 50%;
    left: 2%;
    z-index: 99;
}
header {
height: 15vh;
line-height: 5vh;
background-image: linear-gradient(to right bottom, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
color: #fff;
font-weight: 300;
position: relative;
z-index: 999;
}

.logo img {
    width: 150px;
    margin-top: 20px;
    margin-right: 30px;
}

header a {
    color: #fff;
    margin-left: 30px;
}

header i {
    quotes: 40px; 
    height: 40px;
    padding: 10px;
    text-align: center;
    border-radius: 50%;
    border: 2px solid #fff;
    margin: 0 10px;
}

header button {
    border-radius: 10px;
    font-size: 15px;
    font-weight: bold;
}

.toggle {
    display: none;
}

@media only screen and (max-width: 768px)  {
    header nav .flexSB {
        display: none;
    }
 
    .account  {
        display: none;
    }

    .navMenu-list {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #e50813;
        width: 50%;
        height: 100vh;
        z-index: 999;
    }

    header ul li {
        margin-top: 30px;
        padding: 0 50px;
    }

    .toggle {
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 20px;
        margin: auto;
        cursor: pointer;
        background: none;
    }
}
.home  {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    color: #fff;
}

.homeContainer {
    position: relative;
}

.coverImage {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: -2;
}

.coverImage img {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
}

.homeContainer .content {
    padding: 100px;
}

.homeContainer h1 {
   margin-top: 70px; 
   font-size: 50px;
   text-transform: uppercase;
   font-weight: 900;
   
   background-repeat: repeat;
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
   -webkit-font-smoothing: antialiased;
}

.coverImage::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 88;
    background-image: linear-gradient(to right bottom, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0));
}


.homeContainer .rating {
    margin: 10px 0;
}

.homeContainer p{
    font-size: 14px;
    font-weight: 200;
}

.rating i {
    color: #cfa04d;
    margin-right: 5px;
}

.rating label {
    margin: 0 20px;
}

.rating span {
    background-color: #6c757d;
    padding: 5px;
    color: #fff;
    font-weight: bold;
}

.homeContainer .cast {
    margin: 30px 0;
}

.cast h4 {
    font-weight: 500;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 200;
}

.cast h4 span {
    color: #cfa04d
}

.playButton {
    display: flex;
    align-items: center;
    justify-content: center;
}

.playButton .img {
    position: relative;
}

.change {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.playButton img{
    width: 90px;
    height: 90px;
    margin-right: 20px;
    transition: 0.5s;
}

.playButton button:hover .change {
    opacity: 1;
}


.playButton button {
    cursor: pointer;
    padding: 0;
    margin: 0;
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 2px;
    transition: 0.5s;
}

.margin {
    margin-top: 50%;
}

@media only screen and (max-width: 768px) {
   .homeContainer h1 {
    font-size: 50px;
    margin: 0;
   } 
   .home {
    position: relative;
    margin: 0;
    height: 60vh !important;
   }

   .coverImage {
    width: 100vw;
    height: 70vh !important;
   }

   .home .row {
    width: 100%;
   }

   .home .playButton {
    display: none;
   }

   .margin {
    margin-top: 0 !important;
   }
   
}

@media only screen and (max-width: 368px) {
    .homeContainer h1 {
        font-size: 30px;
        margin: 0;
       } 

}


.container {
   display: grid;
   grid-template-columns: minmax(650px, auto) 1fr;
   grid-gap: 10px;
   gap: 10px;
   padding: 10px;
  
 }
 
 /* Left column for text content */
 .text-column {
   padding: 20px;
   
 }
 
 .text-column h2 {
   font-size: 40px;
   margin-bottom: 10px;
   font-weight: 300;
 }
 

 /* Right column for video content */
 .video-column {
   padding: 40px;
   border-radius: 5px;
 }
 
 .video-column iframe {
   width: 100%;
   height: 100%;
   border: 5px;
   border-radius: 30px;
 }

 span {
   color: #cfa04d;
   font-weight: 400;
 }

 /* Media query for screens smaller than 768px */
@media (max-width: 768px) {
   .container {
   
     grid-template-columns: 1fr; /* Single column for smaller screens */
   }
   
   /* Adjust individual column styles for smaller screens */
   .text-column {
     margin-bottom: 20px; /* Add some space between the columns */
   }

   .text-column h2{
      font-size: 35px;
      align-items: center;
      justify-content: center;
      text-align: center;
   }
 
   .video-column {
     margin-top: 40%;
     margin-bottom: 5px; /* Add some space between the columns */
   }
 }
 
 /* Media query for screens smaller than 576px */
 @media (max-width: 356px) {
   .text-column,
   .video-column {
      margin-top: 40%;
      margin-bottom: 10px;
     padding: 10px; /* Reduce padding for smaller screens */
   }

   .video-column iframe {
      width: 100%;
      height: 100%;
   }
 }
